*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --error-box-background: #fff6f6;
  --input-border: #8b8a8b;
  --input-focus-h: 245;
  --input-focus-s: 100%;
  --input-focus-l: 42%;
  --border-radius: .25rem;
}

main {
  background-color: #d3d3d3;
}

form {
  border-radius: var(--border-radius);
  background-color: #fff;
  padding: 2rem;
  box-shadow: .15rem .15rem .1rem .1rem #789;
}

.N18KEq_form-input {
  border: 2px solid var(--input-border);
  border-radius: var(--border-radius);
  background-color: #fff;
  padding: .25em .5em;
  font-family: inherit;
  font-size: max(16px, 1em);
}

.N18KEq_form-input:focus {
  border-color: hsl(var(--input-focus-h), var(--input-focus-s), var(--input-focus-l));
  box-shadow: 0 0 0 3px hsla(var(--input-focus-h), var(--input-focus-s), calc(var(--input-focus-l)  + 40%), .8);
}

button {
  padding: .25rem .75rem;
}

.N18KEq_column {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.N18KEq_form-group {
  flex-grow: 1;
  gap: .25rem;
}

.N18KEq_column {
  height: 100vh;
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.N18KEq_column .N18KEq_vertically-centered {
  justify-content: center;
}

.N18KEq_column .N18KEq_horizontally-centered {
  align-items: center;
}

.N18KEq_row {
  flex-wrap: wrap;
  gap: 1rem;
  display: flex;
}

.N18KEq_row .N18KEq_right-justified {
  justify-content: flex-end;
}

.N18KEq_error-notification {
  border-radius: var(--border-radius);
  background-color: var(--error-box-background);
  border: .1rem solid red;
  padding: 1rem;
}

.N18KEq_error-text {
  color: red;
  font-size: .75rem;
}

.N18KEq_bold-text {
  font-weight: bold;
}

.N18KEq_input_and_error {
  flex-grow: 1;
}

